@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap");

:root {
  --clr-primary: #050816;
  --clr-primary-dark: #100d25;
  --clr-dark: #151030;
  --clr-secondary: #aaa6c3;
  --ff-primary: 'Poppins', sans-serif;
  --ff-mono: 'Space Mono', monospace;
  --clr-light: #f3f3f3;
}


body {
  overflow-x: hidden !important;
  max-width: 100vw !important;
  background-color: var(--clr-primary);
}





/*################################### Navbar ########################################*/
.header {
  position: fixed;
  height: 90px;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 9999;
  transition: .3s ease-in;
  overflow: hidden;
  background-color: transparent;
}

.activeH {
  background-color: black;
}

.header .navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1240px;
  margin: auto;
  height: 100%;
  padding: 0 1rem;
}

.header .nav-menu a {
  color: var(--clr-light);
  font-size: 20px;
  font-family: var(--ff-mono);
  font-weight: 600;
}

.header .navbar h2 {
  color: #915EFF;
  font-family: var(--ff-primary);
  font-size: 28px;
  font-weight: 600;
  width: 250px;
  height: auto;
}

.header .nav-menu {
  display: flex;
}

.header .nav-item {
  padding: 1rem;
  font-weight: 500;
}

.header .nav-item a:hover {
  color: var(--clr-secondary);
}

.hamburger {
  display: none;
}

.hamburger i {
  color: white;
  font-size: 25px;
  font-weight: bold;
}

ul {
  list-style: none;
}


a {
  text-decoration: none;
}



@media screen and (max-width:940px) {
  .header {
    max-width: 100%;
    background-color: black;
  }

  .header .navbar {
    max-width: 100%;
  }

  .hamburger {
    display: block;
  }

  .nav-menu {
    position: fixed;
    left: -100%;
    top: 90px;
    flex-direction: column;
    background-color: black;
    width: 100%;
    height: 100vh;
    z-index: 999;
    text-align: center;
    transition: .3s;
  }

  .nav-menu.active {
    left: 0;
  }

  /* .nav-item {
      margin: 0.1rem 0;
  } */

  .header .navbar img {
    width: 150px;
  }
}


/* Navbar */


/* Home */

.home-container {
  background-image: url('./assets/herobg.png');
  min-height: 100vh;
  width: 100vw;
  background-size: cover;
  color: #f3f3f3;
  padding: 90px 200px !important;
}

.home-container h1 {
  font-family: var(--ff-primary);
  color: var(--clr-light);
  font-weight: 700;
  font-size: 65px !important;
}

.home-container h1 span {
  font-family: var(--ff-primary);
  color: #915EFF;
  font-size: 65px !important;
}



.home-container h2 {
  font-family: var(--ff-mono);
  font-weight: 600;
  font-size: 45px;
  color: #f3f3f3;
}

.home-container h2 span {
  color: #915EFF;
}

.home-container .btn {
  background-color: transparent;
  border: 2px solid var(--clr-secondary);
  color: var(--clr-light);
  font-size: 18px;
  font-family: var(--ff-mono);
}

.home-container .btn:hover {
  background-color: #915EFF;
  border: 2px solid #915EFF;
  color: var(--clr-light);
  font-size: 18px;
  font-family: var(--ff-mono);
}


/* Home */

/* Skills */
.skills-container {
  background-image: url('./assets/herobg.png');
  min-height: 100vh;
  width: 100vw;
  background-size: auto;
  background-repeat: no-repeat;
  background-position: top !important;
  color: #f3f3f3;
  padding: 100px 150px !important;
}

.skills-container h2 {
  font-size: 35px;
  /* color: #915EFF; */
  color: var(--clr-light);
  font-family: var(--ff-mono);
  font-weight: 400 !important;
}

.skills-container .row .box {
  width: 150px;
  height: 150px;
  border: 1px solid #f3f3f3;
  background-color: #151030;
  border-radius: 10px;
  padding: 5px;
  margin: 5px 10px;
}

.skills-container .row .box img {
  width: 100%;
  height: 100%;
}

.skills-container .row .box:hover {
  cursor: pointer;
  border: 2px solid #915EFF;
  background-color: transparent;
}

/* Skills */

/* Experience */
.experience-container {
  background-image: url('./assets/herobg.png');
  min-height: 100vh;
  width: 100vw;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: right !important;
  color: #f3f3f3;
  padding: 100px 150px !important;
}

.experience-container h2 {
  font-size: 35px;
  /* color: #915EFF; */
  color: var(--clr-light);
  font-family: var(--ff-mono);
  font-weight: 400 !important;
}

.experience-container h3 {
  font-family: var(--ff-primary);
  font-size: 24px;
  font-weight: 500;
}

.experience-container h4 {
  font-family: var(--ff-mono);
  font-size: 20px;
  font-weight: 500;
  margin-bottom: 0px !important;

}

.experience-container p {
  font-family: var(--ff-mono);
}

.vertical-timeline-element-content .vertical-timeline-element-date {
  color: var(--clr-light);
  font-size: 16px !important;
  font-family: var(--ff-mono);
}

/* Experience */

/* Projects */
.projects-container {
  background-image: url('./assets/herobg.png');
  min-height: 100vh;
  width: 100vw;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: left !important;
  color: #f3f3f3;
  padding: 100px 150px;

}

.projects-container h2 {
  font-size: 35px;
  /* color: #915EFF; */
  color: var(--clr-light);
  font-family: var(--ff-mono);
  font-weight: 400 !important;
}

.projects-container .projectBox {
  background-color: var(--clr-dark);
  padding: 10px;
  border: #050816 2px solid;
  border-radius: 15px;
}

.projects-container .projectBox img {
  border-radius: 10px;
  width: 100%;
  padding: 2px;
  margin-bottom: 10px;
}

.projects-container .projectBox h3 {
  font-family: var(--ff-primary);
  font-size: 24px;
  color: var(--clr-light);
}

.projects-container .projectBox p {
  font-family: var(--ff-mono);
  font-size: 20px;
  color: var(--clr-secondary);
}

.projects-container .projectBox .btn {
  background-color: transparent;
  border: 2px solid var(--clr-secondary);
  color: var(--clr-light);
  font-size: 18px;
  font-family: var(--ff-mono);
}

.projects-container .projectBox .btn:hover {
  background-color: #915EFF;
  border: 2px solid #915EFF;
  color: var(--clr-light);
  font-size: 18px;
  font-family: var(--ff-mono);
}

