/* Contact */
.contact-container {
  background-image: url('./assets/herobg.png');
  min-height: 100vh;
  width: 100vw;
  background-size: auto;
  background-repeat: no-repeat;
  background-position: center !important;
  color: #f3f3f3;
  padding: 100px 150px !important;
  display: flex;
  vertical-align: middle !important;
}

.contact-container h2 {
  font-family: var(--ff-mono);
  font-weight: 600;
  font-size: 45px;
  color: #f3f3f3;
}

.contact-container .formBox {
  border: 1px solid #f3f3f3;
  border-radius: 10px;
  padding: 30px;
  background-color: var(--clr-primary-dark);
}

.contact-container .formBox input,
.contact-container .formBox textarea {
  font-family: var(--ff-mono);
  border: 2px solid var(--clr-light);
  background-color: var(--clr-dark);
  margin: 10px 0px;
  color: var(--clr-light);
  font-size: 18px;
}


.contact-container .formBox .btn {
  background-color: transparent;
  border: 2px solid var(--clr-secondary);
  color: var(--clr-light);
  font-size: 18px;
  font-family: var(--ff-mono);
}

.contact-container .formBox .btn:hover {
  background-color: #915EFF;
  border: 2px solid #915EFF;
  color: var(--clr-light);
  font-size: 18px;
  font-family: var(--ff-mono);
}

